@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");

:root {
  --primary: rgba(44, 25, 81, 1);

  --primarybg: rgba(242, 242, 247, 1);
  --secondary: rgba(255, 255, 255, 1);
  --primarydark: rgba(28, 28, 30, 1);
  --secondarydark: #8e8e93;
}
.label {
  margin-top: 2%;
  margin-bottom: 1%;
}
.fullBody {
  display: flex;
  /* width: 100vw; */

  justify-content: center;
}
.button {
  margin-top: 5%;
  background-color: var(--primary);
  color: white;
  height: 30px;
  text-align: center;
  border-radius: 3px;
  border: none;
  font-size: 15px;
  font-family: "Roboto", sans-serif;
  text-decoration: none;
}
.label2 {
  margin-bottom: 5%;
}
.container {
  background-color: var(--secondary);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2%;
  padding-top: 1%;
  padding-bottom: 2%;
  width: 100%;
  min-width: 300px;
  max-width: 1000px;
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
}
.logo {
  width: 28%;
}
.formContainer {
  width: 70%;
  padding: 2%;
  margin: 2%;
  display: flex;
  flex-direction: column;
}
.oneLine {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: baseline;
}

.buttonContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 2%;
}
.spacing {
  width: 8%;
  height: 8%;
}
.arrow {
  width: 2%;
  margin-top: 5%;
}
.page2 {
  height: 100vh;
  width: 90vw;
  margin: 0;
  padding: 0;
}
.page1 {
  width: 90vw;
  margin: 0;
  padding: 1vw;
}
.p1IMG {
  height: 100%;
  order: 1;
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
}
.aboutCont {
  order: 2;
  padding-top: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.textBox {
  order: 1;
  margin-left: 5%;
  text-align: left;
  font-family: "Roboto", sans-serif;
  font-size: 120%;
  font-weight: 500;
  margin-bottom: 10%;
}
.explain {
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  /* background-color: white; */
  display: flex;
  width: 70%;
  height: 70vh;
  padding: 3%;
  margin-top: 5%;

  /* -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2); */
}
.buttons {
  order: 2;
}
@media screen and (max-width: 429px) {
  .container {
    background-color: var(--secondary);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 5%;
    padding-top: 2%;
    padding-bottom: 5%;
    width: 100%;
    min-width: 200px;
    max-width: 1000px;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  }
  .oneLine {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: baseline;
  }
}
.description {
  width: 70%;
}
h1 {
  font-size: 20px;
}
h2 {
  font-size: 15px;
  font-weight: 500;
  color: green;
}
h3 {
  font-size: 14px;
  font-weight: 400;
  font-style: italic;
}
.total {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.letter {
  font-style: normal;
  font-weight: 600;
}
.pbs {
  width: 12%;
}
.pbsDiv {
  width: 70%;
  margin-top: -3%;
}
