@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");

.App {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  transition: all 0.5s ease;
}
.navContainer {
  /* background-color: #f2f2f7; */
  width: 100vw;
  height: 12vw;
  padding-top: 2vw;

  display: flex;
  align-items: center;
}

.logo {
  height: 90%;
  object-fit: contain;
  float: left;
  object-position: left;
  margin-left: 5%;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.navLinkCont {
  text-align: right;
  margin-left: 52%;
}
.navLink {
  text-decoration: none;
  color: #462a7b;
  font-family: "Roboto", sans-serif;
  font-size: 2.2vw;
  font-weight: 500;
}
.navLink:hover {
  color: black;
}

.Screenshots {
  margin: 2vw;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  flex-shrink: 1;
  flex-grow: 0;
}
.Screenshots img {
  display: flex;
  width: 25%;
  height: 54.125%;
  max-height: 433px;
  max-width: 200px;
  min-height: 216.5px;
  min-width: 100px;
  flex-shrink: 5;

  margin: 1vw;
  -webkit-box-shadow: 0px 0px 16px 1px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 16px 1px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 16px 1px rgba(0, 0, 0, 0.2);
  -webkit-animation: fadein 1s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 1s; /* Firefox < 16 */
  -ms-animation: fadein 1s; /* Internet Explorer */
  -o-animation: fadein 1s; /* Opera < 12.1 */
  animation: fadein 1s;
}

.Badge img {
  height: 50px;
  padding-top: 20px;
  margin: 0.5vw;
}

img {
  -ms-interpolation-mode: bicubic;
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@media screen and (max-width: 769px) {
  .navContainer {
    width: 100vw;
    height: 24vw;
    padding-top: 2vw;

    display: flex;
    align-items: center;
  }
  .navLink {
    text-decoration: none;
    color: #462a7b;
    font-family: "Roboto", sans-serif;
    font-size: 5vw;
    font-weight: 500;
  }
  .navLinkCont {
    text-align: right;
    margin-left: 40%;
  }
}
@media screen and (max-width: 429px) {
  .navContainer {
    width: 100vw;
    height: 40vw;
    padding-top: 2vw;

    display: flex;
    align-items: center;
  }
  .navLink {
    text-decoration: none;
    color: #462a7b;
    font-family: "Roboto", sans-serif;
    font-size: 7vw;
    font-weight: 500;
  }
  .navLinkCont {
    text-align: right;
    margin-left: 28%;
  }
  .Screenshots {
    margin: 2vw;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    flex-shrink: 1;
    flex-grow: 0;
  }
  .Screenshots img {
    display: flex;

    height: 368.05px;
    width: 170px;

    margin-top: 3vw;
    -webkit-box-shadow: 0px 0px 16px 1px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 0px 0px 16px 1px rgba(0, 0, 0, 0.2);
    box-shadow: 0px 0px 16px 1px rgba(0, 0, 0, 0.2);
    -webkit-animation: fadein 1s; /* Safari, Chrome and Opera > 12.1 */
    -moz-animation: fadein 1s; /* Firefox < 16 */
    -ms-animation: fadein 1s; /* Internet Explorer */
    -o-animation: fadein 1s; /* Opera < 12.1 */
    animation: fadein 1s;
  }
}
